@import url('https://fonts.googleapis.com/css2?family=Chakra+Petch:ital,wght@0,400;0,700;1,400;1,700&family=Cousine:ital,wght@0,400;0,700;1,400;1,700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}

html {
    scroll-behavior: smooth;
}

body {
    background-color: dimgrey;
    background-color: rgb(80, 80, 80);
    background-color: #b8b8b8;
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
}

.splash {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #040D25;
    z-index: 5;
    transition: transform 1s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.remove-splash {
    transform: translateY(-100%);
}

.splash img {
    width: 90px;
}

.content__section h1 {
    color: yellow;
    font-size: 14rem;
    animation: glitch 700ms infinite;
}

.container {
    width: 100%;
    height: 100vh;
    overflow-y: scroll;
    scroll-snap-type: y mandatory;
}

.content__section {
    height: 100vh;
    scroll-snap-align: start;
    scroll-snap-stop: always;
}

.content__section:nth-of-type(1) {
    background-image: linear-gradient(to left bottom, #000562, #000b58, #000f4d, #000f42, #000f36, #040f34, #071031, #0a102f, #111136, #1a103c, #250d41, #37084d);
    overflow: hidden;
    position: relative;
}

.content__section:nth-of-type(2) {
    background-color: #171a49;
    display: flex;
    justify-content: center;
    align-items: center;
}

.content__section:nth-of-type(3) {
    background-image: linear-gradient(to left bottom, #571dfb, #3427d7, #1528b2, #00258c, #001f67, #011f65, #032064, #042062, #212683, #4428a2, #6b22bf, #9600d8);
    display: flex;
    justify-content: center;
    align-items: center;
}

.content__section:nth-of-type(4) {
    background-image: linear-gradient(to left bottom, #370d4a, #2f124b, #27154b, #1f184a, #171a49, #181e55, #171e5a, #101b57, #1c186d, #3f1e91, #5f21a1, #3a0b4e);
    display: flex;
    justify-content: center;
    align-items: center;
}

#cursor {
    position: absolute;
    width: 55px;
    height: 55px;
    border-radius: 50%;
    background-color: rgb(255, 0, 190);
    pointer-events: none;
    mix-blend-mode: difference;
    z-index: 100;
}


/* RZEŹBA - PARALAXA */

.sculpture {
    background-image: url('/public/photo2.png');
    height: 100vh;
    width: auto;
    background-position: top;
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
    overflow: hidden;
    margin-left: 7.5%;
    animation: transitionFromBottom 2.1s ease-in;
}

.parallax-container,
.parallax-image {
    overflow: hidden;
}


/*  NAWIGACJA  */

.sections__nav {
    transform: rotate(90deg);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 99;
    width: fit-content;
    width: 70px;
    display: none;
    position: absolute;
    top: 45%;
    opacity: 0;
    animation: fadeIn 0.15s ease-in-out forwards;
    color: #fff;
    margin-left: auto;
}

.sections__nav.nav__open {
    display: flex;
    opacity: 1;
}

.sections__nav li {
    list-style: none;
    font-family: 'Cousine', monospace;
    font-size: 1.5rem;
    cursor: pointer;
    transition: .215s;
    user-select: none;
    position: relative;
    align-items: center;
    display: flex;
    padding: 2.5% 15%;
    margin: 0 12.5px;
}

.sections__nav li::after {
    content: "";
    position: absolute;
    right: 8px;
    left: 8px;
    height: 4px;
    background: rgb(0, 255, 255);
    transform: scaleX(0);
    transform-origin: right;
    transition: transform 0.5s cubic-bezier(0.4, 0.0, 0.2, 1);
}

.sections__nav li:hover::after {
    transform: scaleX(1);
    transform-origin: left;
}


/*  HAMBURGER MENU  */

.navigation {
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
    margin-top: 2rem;
    margin-right: 2.5rem;
}

label {
    display: flex;
    flex-direction: column;
    width: 70px;
    cursor: pointer;
}

label span {
    background: #fff;
    border-radius: 10px;
    height: 7px;
    margin: 7px 0;
    transition: .4s cubic-bezier(0.68, -0.6, 0.32, 1.6);
    animation: scaleIn 5s;
}

span:nth-of-type(1) {
    width: 50%;
}

span:nth-of-type(2) {
    width: 100%;
}

span:nth-of-type(3) {
    width: 75%;
}

input[type="checkbox"] {
    display: none;
}

input[type="checkbox"]:checked~span:nth-of-type(1) {
    transform-origin: bottom;
    transform: rotatez(45deg) translate(8px, 0px)
}

input[type="checkbox"]:checked~span:nth-of-type(2) {
    transform-origin: top;
    transform: rotatez(-45deg)
}

input[type="checkbox"]:checked~span:nth-of-type(3) {
    transform-origin: bottom;
    width: 50%;
    transform: translate(30px, -11px) rotatez(45deg);
}

.app,
.todos {
    margin-top: 10vh;
}

.tasks {
    list-style: none;
}

.task {
    display: flex;
    justify-content: center;
    align-items: center;
}

.name {
    font-family: 'Chakra Petch', sans-serif;
    font-size: 8.75rem;
    font-weight: bold;
    font-style: italic;
    color: #fff;
    position: absolute;
    top: 10%;
    left: 3.5%;
    user-select: none;
    opacity: 0;
    animation: fadeIn 0.75s ease-in-out forwards;
}

.name span {
    font-family: 'Chakra Petch', sans-serif;
    margin-left: 32.5%;
    opacity: 0;
    animation: fadeIn 2s ease-in-out forwards;
}

.job__title {
    font-size: 2.2rem;
    top: 70%;
    left: 6%;
    font-family: 'Chakra Petch', sans-serif;
    font-family: 'Cousine', monospace;
    color: #fff;
    position: absolute;
    font-weight: bold;
    font-style: italic;
    opacity: 0;
    animation: fadeIn 0.8s ease-in-out forwards;
}

.job__title.visible {
    opacity: 1;
}

.name,
.name span:nth-child(1) {
    animation-delay: 0.15s;
}

.name,
.name span:nth-child(2) {
    animation-delay: 0.3s;
}

.name,
.name span:nth-child(3) {
    animation-delay: 0.45s;
}

.name,
.name span:nth-child(4) {
    animation-delay: 0.6s;
}

.name,
.name span:nth-child(5) {
    animation-delay: 0.75s;
}

.name,
.name span:nth-child(6) {
    animation-delay: 0.9s;
}


/* ANIMACJA POJAWIANIA SIĘ IMIENIA & NAZWISKA */

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes scaleIn {
    from {
        scale: 0;
    }

    to {
        scale: 1;
    }
}

@keyframes glitch {
    0% {
        text-shadow: 0.05em 0 0 #00fffc, -0.03em -0.04em 0 #fc00ff, 0.025em 0.04em 0 #fffc00;
    }

    15% {
        text-shadow: 0.05em 0 0 #00fffc, -0.03em -0.04em 0 #fc00ff, 0.025em 0.04em 0 #fffc00;
    }

    16% {
        text-shadow: -0.05em -0.025em 0 #00fffc, 0.025em 0.035em 0 #fc00ff, -0.05em -0.05em 0 #fffc00;
    }

    49% {
        text-shadow: -0.05em -0.025em 0 #00fffc, 0.025em 0.035em 0 #fc00ff, -0.05em -0.05em 0 #fffc00;
    }

    50% {
        text-shadow: 0.05em 0.035em 0 #00fffc, 0.03em 0 0 #fc00ff, 0 -0.04em 0 #fffc00;
    }

    99% {
        text-shadow: 0.05em 0.035em 0 #00fffc, 0.03em 0 0 #fc00ff, 0 -0.04em 0 #fffc00;
    }

    100% {
        text-shadow: -0.05em 0 0 #00fffc, -0.025em -0.04em 0 #fc00ff, -0.04em -0.025em 0 #fffc00;
    }
}

@keyframes transitionFromBottom {
    from {
        opacity: 0;
        transform: translateY(100%);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}